import React from 'react'
import { Animated } from 'react-native'

/*

    A simple module to fade in and out a component.
    props:
      start={true} means it will execute
      delay={timeInMs} is the delay until it starts

    otherwise, make a react ref to this element and then just
    execute the functions

*/

export default class Fade extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      a_visibility: new Animated.Value(0)
    }
  }

  componentWillMount() {
    if(this.props.startVisible)
     this.setSate({ a_visibility: new Animated.Value(1)})

  }

  componentDidMount() {
    if(!this.props.start) return
    if(this.props.startVisible) this.fadeOut(this.props.delay)
    else                        this.fadeIn(this.props.delay)
  }

  fadeIn(delay) {
    console.log("ANIMATE: FAdeIn")
    setTimeout(() => {
      Animated.timing(
        this.state.a_visibility,
        { toValue : 1, duration: 500}
      ).start()
    }, delay)
  }

  fadeOut(delay) {
    console.log("ANIMATE: FAdeOut")
    setTimeout(() => {
      Animated.timing(
        this.state.a_visibility,
        { toValue : 0, duration: 500}
      ).start()
    }, delay)
  }

  render() {
    return(
      <Animated.View style={{ opacity: this.state.a_visibility }}>
        {this.props.children}
      </Animated.View>
    )
  }
}



